<template>
  <div style="display: flex; overflow: auto;">
    <div class="intro-text" style="position: absolute;min-height: calc(100vh - 60px);height: auto;width: 155px; background-color: #ecedf1;flex-shrink: 0;">
      <div style="font-size: 18px; font-weight: bold; text-align: center; margin: 10px 0; color: grey;">设备信息</div>
      <el-menu :default-openeds="['info']" :collapse="isCollapse" :collapse-transition="false" router background-color="#ecedf1" text-color="#222221"
               active-text-color="#fff" style="border: none;" :default-active="$route.path">
        <el-menu-item index="/deviceList">
          <i class="el-icon-caret-right"></i>
          <span slot="title">设备列表</span>
        </el-menu-item>
        <el-menu-item index="/childDevice">
          <i class="el-icon-caret-right"></i>
          <span slot="title">部件表</span>
        </el-menu-item>
        <el-menu-item index="/meterList" >
          <i class="el-icon-caret-right"></i>
          <span slot="title">仪表列表</span>
        </el-menu-item>
        <div style="font-size: 18px; font-weight: bold; text-align: center; margin: 10px 0; color: grey;">点检标准录入</div>
        <el-menu-item index="/entryForm">
          <i class="el-icon-caret-right"></i>
          <span slot="title">标准录入</span>
        </el-menu-item>
        <el-menu-item index="/standardView" >
          <el-dropdown placement="bottom">
            <div>
              <i class="el-icon-location-outline"></i>
              <span slot="title">设备标准</span>
            </div>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item @click.native="$router.push('/standardView')">运行工标准</el-dropdown-item>
              <el-dropdown-item @click.native="$router.push('/standardView2')">维修工标准</el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </el-menu-item>
        <el-menu-item index="/meterList1" >
          <el-dropdown placement="bottom">
            <div>
              <i class="el-icon-location-outline"></i>
              <span slot="title">仪表标准</span>
            </div>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item @click.native="$router.push('/meterList1')">运行工标准</el-dropdown-item>
              <el-dropdown-item @click.native="$router.push('/meterList2')">维修工标准</el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </el-menu-item>
      </el-menu>
      <div style="padding-top: 40px">
        <img src="../../assets/金色时代.png" height="234" width="153"/>
      </div>
    </div>
      <el-card style="display: flex;background-color: white;width: 1200px;height: 600px;margin: 3% 0 0 14%;overflow: auto">
        <!-- 录入形式、勾选设备、填写数据三个页面合成一起-->
        <div>
          <div style="margin: 20px 0 20px 100px ;width: 1000px">
            <el-steps :active="active" finish-status="success" align-center>
              <el-step title="录入形式" font-size="20%" ></el-step>
              <el-step title="勾选设备" font-size="30px"></el-step>
              <el-step title="标准录入" font-size="30px"></el-step>
            </el-steps>
          </div>
          <div v-if="active === 0">
            <div style="display: flex;">
              <div class="smallbox" style="width: 360px;height:144px;text-align: center; margin-top: 70px;margin-left:16%;border-radius: 16px" @click="setEntryType('运行工')">
                <img src="../../assets/运行.png" width="360" height="144" border-radius="16px" background="linear-gradient(-30deg, #AD89F3 40%, #8757F4 100%)"/>
              </div>
              <div class="smallbox" style="width: 360px;height:144px;text-align: center; margin-top: 70px;margin-left:12%;border-radius: 16px" @click="setEntryType('维修工')">
                <img src="../../assets/维修.png" width="360" height="144" border-radius="16px" background="linear-gradient(-30deg, #AD89F3 40%, #8757F4 100%)"/>
              </div>
            </div>
            <div style="display: flex;">
              <div class="smallbox" style="width: 360px;height:144px;text-align: center; margin-top: 70px;margin-left:16%;border-radius: 16px" @click="to1()">
                <img src="../../assets/设备.png" width="360" height="144" border-radius="16px" background="linear-gradient(-30deg, #AD89F3 40%, #8757F4 100%)"/>
              </div>
              <div class="smallbox" style="width: 360px;height:144px;text-align: center; margin-top: 70px;margin-left:12%;border-radius: 16px" @click="to2()">
                <img src="../../assets/仪表.png" width="360" height="144" border-radius="16px" background="linear-gradient(-30deg, #AD89F3 40%, #8757F4 100%)"/>
              </div>
            </div>
          </div>

          <!-- 选择设备 -->
          <!-- 模糊查询 -->
          <div v-if="active === 1" style="margin-left: 5%">
            <div style="margin: 0 0 20px 20px;">
              <el-input style="width: 320px;height: 32px; margin-right: 10px;" placeholder="请输入搜索内容" v-model="searchText"></el-input>
              <el-button type="primary" @click="getDevice(1)">搜索</el-button>
              <el-button type="info" @click="reset">重置</el-button>
            </div>
            <!-- 设备 -->
            <div style="display: flex;">
              <div style="width: 400px;height: 380px;background: #FFFFFF; margin-left: 17px;">
                <el-table :data="equipmentsList" border height="380" :header-cell-style="{backgroundColor: 'aliceblue', color:'#666'}" @selection-change="handleSelectionChange">
                  <el-table-column type="selection" width="55" align="center" :show-overflow-tooltip="true"></el-table-column>
                  <el-table-column prop="equipmentName" label="设备名称" align="center" :show-overflow-tooltip="true"></el-table-column>
                  <el-table-column prop="type" label="设备类型" align="center" :show-overflow-tooltip="true"></el-table-column>
                </el-table>
                <div style="margin-top: 10px;display: flex">
                  <el-pagination
                      @current-change="handleCurrentChangeDevice"
                      :current-page="equipmentsPageNum"
                      :pager-count="3"
                      :page-size="equipmentsPageSize"
                      layout="total, prev, pager, next"
                      :total="equipmentsTotal"
                  ></el-pagination>
                  <div style="position: relative;left: 30px">
                    <el-button plain type="primary" @click="equipmentsSelect()">选择</el-button>
                  </div>
                </div>
              </div>
              <!--            选择的设备-->
              <div style="width:240px">
                <el-card style="width: 240px;height: 380px;margin-left: 22px">
                  <span style="font-size: 18px;font-weight: bold;justify-content: center;display: flex">已选设备</span><br/>
                  <div style="height: 360px;overflow: auto">
                    <el-tag v-for="equipment in formEquipmentsList" style="margin: 2px;" hit="true" closable @close="meterRemove(equipment)">
                      {{ equipment.equipmentName}}
                    </el-tag>
                  </div>
                </el-card>
              </div>
              <!-- 子设备 -->
              <div style="display: flex">
                <div style="width: 400px;height: 380px;background: #FFFFFF;margin-left: 40px;">
                  <el-table :data="childDeviceList" border height="380" :header-cell-style="{backgroundColor: 'aliceblue', color:'#666'}">
                    <el-table-column type="selection" width="50" align="center" :show-overflow-tooltip="true"></el-table-column>
                    <el-table-column prop="childName" label="部件名称" align="center">
                    </el-table-column>
                    <el-table-column label="选择" align="center" width="100">
                      <template v-slot="scope">
                        <el-button size="mini" type="primary" plain @click="childDeviceSelect(scope.row)">选择</el-button>
                      </template>
                    </el-table-column>
                  </el-table>
<!--                  <div style="margin-top: 10px;display: flex">-->
<!--                    <el-pagination-->
<!--                        @current-change="handleCurrentChangeDevice"-->
<!--                        :current-page="equipmentsPageNum"-->
<!--                        :pager-count="3"-->
<!--                        :page-size="equipmentsPageSize"-->
<!--                        layout="total, prev, pager, next"-->
<!--                        :total="equipmentsTotal"-->
<!--                    ></el-pagination>-->
<!--                  </div>-->
                </div>
              </div>
            </div>
            <!--          <div style="text-align: center;">-->
            <!--            <el-button type="primary" @click="completeSelection">完成</el-button>-->
            <!--          </div>-->
          </div>
          <!-- 标准录入 -->
          <div v-if="active === 2">
            <div style="display: flex;">
              <div style="width: 90%;">
                <!-- 判断型点检标准录入 -->
                <div style="font-size: 18px; margin-bottom: 1px;margin-left: 12%;font-weight: bold" id="1">判断型点检标准录入</div>

                <el-card class="box-card" style="width: 950px;height: 300px;background: #FFFFFF;margin-bottom: 20px;margin-left: 12%">
                  <div style="border-width: 5px; height: 300px; overflow: auto;">
                    <div style="display: flex; justify-content: flex-end; position: relative; top: -16px;">
                      <div style="cursor: pointer; width: 30px;margin-top: 13px" @click="addFormRow"><i class="el-icon-plus" style="font-size: 20px;"></i></div>
                    </div>
                    <el-form :model="form" :label-position="labelPosition" label-width="100px" label-size="14px" style="padding-right: 20px;">
                      <div v-for="(row, index) in formRows" :key="index" style="display: flex;">
                        <el-form-item :label="'点检项名称' + (index + 1) + ':'" prop="parameter" style="padding-right: 10px;">
                          <el-input v-model="row.parameter" placeholder="请输入点检项名称" style="width: 262px;height: 32px;background: #FFFFFF;"></el-input>
                        </el-form-item>
                        <el-form-item :label="'开机标准描述' + (index + 1) + ':'" prop="standardDesUp">
                          <el-input type="textarea" resize="none" :autosize="{ minRows: 1, maxRows: 10}" v-model="row.standardDesUp" placeholder="请输入开机点检标准描述" style="width: 262px;height: 32px;background: #FFFFFF;margin-left: 1px;"></el-input>
                        </el-form-item>
                        <el-form-item :label="'关机标准描述' + (index + 1) + ':'" prop="standardDesDown">
                          <el-input type="textarea" resize="none" :autosize="{ minRows: 1, maxRows: 10}" v-model="row.standardDesDown" placeholder="请输入关机点检标准描述" style="width: 262px;height: 32px;background: #FFFFFF;margin-left: 1px;"></el-input>
                        </el-form-item>
                      </div>
                    </el-form>
                  </div>
                </el-card>
                <!-- 数值表 -->
                <div style="font-size: 18px;margin-left: 12%;font-weight: bold" id="2">数值型点检标准录入</div>
                <el-card class="box-card" style="width: 950px;height: 300px;background: #FFFFFF; overflow: auto;margin-left: 12%">
                  <div style="border-width: 5px; height: 300px; overflow: auto;">
                    <div style="display: flex; justify-content: flex-end; position: relative; top: -16px;">
                      <div style="cursor: pointer; width: 30px;margin-top: 13px" @click="addFormRow1"><i class="el-icon-plus" style="font-size: 20px;"></i></div>
                    </div>
                    <el-form :model="form" :label-position="labelPosition" label-width="100px" style="padding-right: 20px;">
                      <div v-for="(row, index) in formRows1" :key="index" style="display: flex;">
                        <el-form-item :label="'点检项名称' + (index + 1) + ':'" prop="parameter" style="padding-left: 1px;">
                          <el-input v-model="row.parameter" placeholder="请输入点检项名称" style="width: 200px;height: 32px;background: #FFFFFF;"></el-input>
                        </el-form-item>
                        <el-form-item :label="'开机范围' + (index + 1) + ':'" prop="powerUp">
                          <el-input v-model="row.powerUp" placeholder="数据范围" style="width: 200px;height: 32px;background: #FFFFFF;margin-left: 10px;"></el-input>
                        </el-form-item>
                        <el-form-item :label="'关机范围' + (index + 1) + ':'" prop="powerDown">
                          <el-input v-model="row.powerDown" placeholder="数据范围" style="width: 200px;height: 32px;background: #FFFFFF;margin-left: 10px;"></el-input>
                        </el-form-item>
                        <el-form-item :label="'开机标准描述' + (index + 1) + ':'" prop="standardDesUp">
                          <el-input type="textarea" resize="none" :autosize="{ minRows: 1, maxRows: 3}" v-model="row.standardDesUp" placeholder="请输入开机点检标准描述" style="width: 200px;height: 32px;background: #FFFFFF;margin-left: 10px;"></el-input>
                        </el-form-item>
                        <el-form-item :label="'关机标准描述' + (index + 1) + ':'" prop="standardDesUp">
                          <el-input type="textarea" resize="none" :autosize="{ minRows: 1, maxRows: 3}" v-model="row.standardDesDown" placeholder="请输入关机点检标准描述" style="width: 200px;height: 32px;background: #FFFFFF;margin-left: 10px;"></el-input>
                        </el-form-item>
                      </div>
                    </el-form>
                  </div>
                </el-card>
              </div>
            </div>
            <div style="text-align: center;">
              <el-button style="background-color: #54ba9c; border-color: #54ba9c; margin-top: 10px;" @click="completeSelection" type="success">完成</el-button>
            </div>
          </div>
          <!--仪表标准录入-->
          <div v-if="active === 3">
            <div style="display: flex; justify-content: center; overflow: auto;width: 100%">
              <el-card class="box-card" style="background-color: #f6f7f9; margin-bottom: 15px;width: 70%">
                <div style="border-width: 5px;height: 450px; overflow: auto;">
                  <div style="font-size: 22px; margin-bottom: 10px;justify-content: center;display: flex;font-weight: bold">仪表点检标准录入</div>
                  <div style="display: flex; justify-content: flex-end; position: relative; top: -40px;">
                    <div style="cursor: pointer; width: 30px;" @click="addFormRow2"><i class="el-icon-plus" style="font-size: 20px;"></i></div>
                  </div>
                  <el-form :model="form" label-width="100px" style="padding-right: 20px;">
                    <div v-for="(row, index) in formRows2" :key="index" >
                      <el-form-item :label="'仪表名称' + (index + 1) + ':'" prop="meterName" style="padding-right: 10px;">
                        <el-input v-model="row.meterName" placeholder="请输入仪表名称" style="width: 180px;"></el-input>
                      </el-form-item>
                      <div style="display: flex">
                        <el-form-item :label="'仪表编号' + (index + 1) + ':'" prop="meterNum" style="padding-right: 10px;">
                          <el-input v-model="row.meterNum" placeholder="请输入仪表编号" style="width: 140px;"></el-input>
                        </el-form-item>
                        <el-form-item :label="'所属部门' + (index + 1) + ':'" prop="department" style="padding-right: 10px;">
                          <el-input v-model="row.department" placeholder="请输入所属部门" style="width: 140px;"></el-input>
                        </el-form-item>
                        <el-form-item :label="'仪表类型' + (index + 1) + ':'" prop="type" style="padding-right: 10px;">
                          <el-input v-model="row.type" placeholder="请输入仪表类型" style="width: 140px;"></el-input>
                        </el-form-item>
                      </div>
                      <div style="display: flex">
                        <el-form-item :label="'点检标准' + (index + 1) + ':'" prop="standards" style="padding-right: 10px;">
                          <el-input v-model="row.standards" placeholder="请输入点检标准" style="width: 200px;"></el-input>
                        </el-form-item>
                        <el-form-item :label="'标准描述' + (index + 1) + ':'" prop="standardDes">
                          <el-input type="textarea" resize="none" :autosize="{ minRows: 1, maxRows: 10}" v-model="row.standardDes" placeholder="请输入点检标准描述" style="width: 360px;"></el-input>
                        </el-form-item>
                      </div>
                    </div>
                  </el-form>
                </div>
              </el-card>
            </div>
            <div style="display: flex;justify-content: center">
              <el-button type="primary" style="background-color: #54ba9c; border-color: #54ba9c; margin-top: 10px;" @click="re0">上一步</el-button>
              <el-button type="primary" style="background-color: #54ba9c; border-color: #54ba9c; margin-top: 10px;" @click="completeSelection2">完成</el-button>
            </div>
          </div>
        </div>
      </el-card>
    </div>
    <!-- 选择录入形式页面-->

</template>
<script>
export default {
  name: "EntryForm",
  data() {
    return {
      labelPosition: 'top',
      equipmentsList: [],
      equipmentsType: '',
      active: 0,
      equipmentsPageNum: 1,
      equipmentsPageSize: 10,
      equipmentsTotal: '',
      childDevicePageNum: 1,
      childDevicePageSize: 10,
      childDeviceTotal: '',
      formEquipmentsList: [],//选择的设备列表
      formChildDeviceList: [],//选择的子设备列表
      entryType: '',  // 用于区分是运行工还是维修工
      childDeviceList: [],
      equipmentNum: [],
      pageSize: 10,
      currentPage: 1,
      form: {},
      temporaryData: [] ,// 用于暂存数据的数组
      temporaryData2: [] ,// 用于暂存数据的数组
      formRows: [
        {
          checkName: '',
          standardDes: '',
          type: '判断',
        }
      ],
      formRows1: [
        {
          checkName: '',
          powerUp: '',
          powerDown: '',
          standardDes: '',
          type: '数值',
        }
      ],
      formRows2: [
        {
          meterName: '',
          meterNum: '',
          type: '',
          department: '',
          standards: '',
          standardDes: '',
        }
      ],
      selectedEquipment: null,
      selectedChildDevices: [],
      searchText: '',
    }
  },
methods: {
    setEntryType(type) {
      this.entryType = type;
      // 重置所有盒子的样式
      var boxes = document.querySelectorAll('.smallbox');
      boxes.forEach(function(box) {
        box.classList.remove('clicked');
      });

      // 为被点击的盒子及其内部元素添加 hover 类
      event.currentTarget.classList.add('clicked');
    },
    meterRemove(num){
      console.log('执行删除'+num);
      this.formEquipmentsList.splice(this.formEquipmentsList.indexOf(num),1);
      if (this.formEquipmentsList.length===0){
        this.equipmentsType = '';
        this.getDevice(1);
      }
    },
    remove(){
      this.equipmentsType='';
      this.formEquipmentsList=[];
      this.formChildDeviceList=[];
      this.temporaryData2 = [];
    },
  to1() {
    if (this.entryType !== '运行工' && this.entryType !== '维修工') {
      this.$message.warning('请选择角色');
      return; // 阻止继续执行下面的代码
    }
    // 如果选中了运行工或维修工，继续执行你的逻辑
    this.getDevice(1);
    this.active = 1;
  },
  to2() {
    if (this.entryType !== '运行工' && this.entryType !== '维修工') {
      this.$message.warning('请选择角色');
      return; // 阻止继续执行下面的代码
    }
    // 如果选中了运行工或维修工，继续执行你的逻辑
    this.getChildDevice(1);
    this.active = 3;
  },
    re0() {
      this.formEquipmentsList=[];
      this.formChildDeviceList=[];
      this.formRows2 = [{ meterName: '', meterNum: '', type: '', department: '', standards: '', standard_dDes:''}];
      this.active = 0;
    },
    next() {
      if (this.active === 2) {
        this.active--;
      }
    },
    reset() {
      this.searchText = '';
      this.getDevice(1);
    },
    getDevice(pageNum) {
      if (pageNum) this.equipmentsPageNum = pageNum;
      this.$request.get('/deviceList/selectByPage', {
        params: {
          pageNum: this.equipmentsPageNum,
          pageSize: this.equipmentsPageSize,
          searchText: this.searchText,
          type: this.equipmentsType,
          department: ''
        }
      }).then(res => {
        this.equipmentsList = res.data.records;
        this.equipmentsTotal = res.data.total;
      });
    },
    handleCurrentChangeDevice(pageNum) {
      this.getDevice(pageNum);
    },
    getChildDevice(equipmentNum) {
      console.log('查询子设备列表中');
      console.log(equipmentNum);
      this.$request.get('/childDevice/getList', {
        params: {
          deviceNum: equipmentNum
        }
      }).then(res => {
        this.childDeviceList = res.data;
        console.log(this.childDeviceList);
      }).catch(error => {
        console.error("Error fetching child devices: ", error);
      });
    },
    handleCurrentChangeChildDevice(pageNum) {
      this.getChildDevice(pageNum);
    },
    handleSelectionChange(rows) {
      // 当前选中的所有的行数据
      this.ids = rows.map(v => v.id);
      // 存储选中的行数据
      this.selectedRows = rows;
    },

    equipmentsSelect() {
      if (this.selectedRows.length === 0) {
        this.$message.warning('请选择至少一个设备');
        return;
      }
      // 获取第一个设备的类型作为标准类型
      const standardType = this.selectedRows[0].type;
      // 遍历选中的行数据并添加到formEquipmentsList中（避免重复）
      this.selectedRows.forEach(row => {
        if (row.type !== standardType) {
          this.$message.warning('请选择类型相同的设备');
          return;
        }
        if (!this.formEquipmentsList.some(equipment => equipment.id === row.id)) {
          this.formEquipmentsList.push(row);
          this.updateEquipment(row);
        } else {
          // 如果已存在，则给出警告提示
          this.$message.warning(`设备 ${row.id} 已选择`);
        }
      });

      console.log(this.formEquipmentsList);
    },

    updateEquipment(row) {
      // 统一处理设备选择后的逻辑
      this.selectedEquipment = row;
      this.equipmentsType = row.type;

      if (this.equipmentsType === '') {
        this.getDevice(1);
      }

      this.selectedEquipment.childName = ''; // 在选定设备时，初始化子设备名称为空字符串
      this.getChildDevice(row.equipmentNum);
    },

    childDeviceSelect(row) {
      // 检查子设备是否已经被选定
      if (!this.formChildDeviceList.includes(row)) {
        // 如果不存在，才添加到数组中
        this.formChildDeviceList.push(row);
        // 清空表单数据
        this.formRows = [{ checkName: '', standardDes: '', type: '判断' }];
        this.formRows1 = [{ checkName: '', powerUp: '', powerDown: '', standardDes: '', type: '数值' }];
        this.active = 2; // 跳转到第三个页面
      } else {
        // 如果已存在，则给出警告提示
        this.$message.warning('该子设备已选择');
      }
    },
    addFormRow() {
      this.formRows.push({
        checkName: '',
        standardDes: '',
        type: '判断',// 为判断类型设置 type 为 '判断'
      });
    },
    addFormRow1() {
      this.formRows1.push({
        checkName: '',
        powerUp: '',
        powerDown: '',
        standardDes: '',
        type: '数值', // 为数值类型设置 type 为 '数值',
      });
    },
    addFormRow2() {
      this.formRows2.push({
        meterName: '',
        meterNum: '',
        type: '',
        department: '',
        standards: '',
        standardDes: '',
      });
    },
    completeSelection() {
      // 清空 temporaryData 数组
      this.temporaryData = [];
      // 确定录入类型
      let apiUrl = this.entryType === '运行工' ? '/standardView/addBatch' : '/standardView2/addBatch';
      let pushRout = this.entryType === '运行工' ? '/standardView' : '/standardView2';
      // 遍历选定的设备和子设备，填充 temporaryData 数组
      this.formEquipmentsList.forEach(selectedEquipment => {
        this.formChildDeviceList.forEach(selectedChildDevice => {
          let tempData = [];
          let allFormRows = [...this.formRows, ...this.formRows1];
          allFormRows.forEach(row => {
            if (this.isRowFilled(row)) {
              let rowData = {
                equipmentName: selectedEquipment.equipmentName,
                childEquipment: selectedChildDevice.childName,
                nfcNum: selectedChildDevice.nfcNum,
                parameter: row.parameter,
                // entry: entryType,
                type: row.type,
                ...row
              };
              tempData.push(rowData);
            }
          });
          this.temporaryData.push(...tempData);
        });
      });
      // 如果没有填写任何数据，则给出警告提示并阻止页面跳转
      if (this.temporaryData.length === 0) {
        this.$message.warning('请填写至少一项数据');
        return;
      }
      // 保存数据并重置页面状态
      this.$request.post(apiUrl, this.temporaryData)
          .then(response => {
            this.$message.success('数据保存成功');
            console.log('保存的数据:', this.temporaryData);
            // 清空 temporaryData 数组
            this.temporaryData = [];
            // 可选地，你可以在这里重置表单或导航到另一个页面
            this.$router.push(pushRout);
            this.remove();
          })
          .catch(error => {
            this.$message.error('数据保存失败');
            console.error('保存数据时出错:', error);
          });
    },
    completeSelection2() {
      // 确定录入类型
      let apiUrl2 = this.entryType === '运行工' ? '/meterList1/addBatch' : '/meterList2/addBatch';
      let pushRout2 = this.entryType === '运行工' ? '/meterList1' : '/meterList2';
      let allFormRows = [...this.formRows2];
      allFormRows.forEach(row => {
        if (this.isRowFilled(row)) {
          let rowData = {
            meterName: row.meterName,
            meterNum: row.meterNum,
            type: row.type,
            department: row.department,
            standards: row.standards,
            standardDes: row.standardDes,
          };
          this.temporaryData2.push(rowData);
        }
      });
      // 如果没有填写任何数据，则给出警告提示并阻止页面跳转
      if (this.temporaryData2.length === 0) {
        this.$message.warning('请填写至少一项数据');
        return;
      }
      // 保存数据并重置页面状态
      this.$request.post('/meterList/addBatch', this.temporaryData2)
          .then(response => {
            // this.$message.success('数据保存成功');
            console.log('保存的数据:', this.temporaryData2);
          })
          .catch(error => {
            this.$message.error('数据保存失败');
            console.error('保存数据时出错:', error);
          });
      // 保存数据并重置页面状态
      this.$request.post(apiUrl2, this.temporaryData2)
          .then(response => {
            this.$message.success('数据保存成功');
            console.log('保存的数据:', this.temporaryData2);
            // 清空 temporaryData 数组
            this.temporaryData2 = [];
            // 可选地，你可以在这里重置表单或导航到另一个页面
            this.$router.push(pushRout2);
            this.remove();
          })
          .catch(error => {
            this.$message.error('数据保存失败');
            console.error('保存数据时出错:', error);
          });
    },
// 辅助方法：检查行是否填写了数据
    isRowFilled(row) {
      // 检查除了 type 以外的其他字段是否为空
      for (const key in row) {
        if (key !== 'type' && row[key].trim() !== '') {
          return true;
        }
      }
      return false;
    },
  },
  beforeRouteEnter(to, from, next) {
    // 执行刷新逻辑
    next(vm => {
      if (to.path === '/standardView') {
        vm.$nextTick(() => {
        });
      }
    });
  },

}
</script>

